/**
 * Feature flags with URL override support for testing
 */

export function layoutEnabled(): boolean {
  // Allow URL override for testing: ?layout=1 or ?layout=0
  const u = new URL(window.location.href);
  const q = u.searchParams.get('layout');
  if (q === '1') return true;
  if (q === '0') return false;

  // Default: check build-time env
  return import.meta.env.VITE_LAYOUT_ENABLED === '1';
}

/**
 * Backend API availability flag
 * Controls whether to make calls to /api/layout, /api/auth/me, etc.
 * Set VITE_BACKEND_ENABLED=0 in prod until API is live to avoid 502s
 */
export function backendEnabled(): boolean {
  // Allow URL override for testing: ?backend=1 or ?backend=0
  const u = new URL(window.location.href);
  const q = u.searchParams.get('backend');
  if (q === '1') return true;
  if (q === '0') return false;

  // Default: check build-time env (default to enabled for backward compatibility)
  return import.meta.env.VITE_BACKEND_ENABLED !== '0';
}
